import { Card, Tabs, List } from 'antd';
import Title from 'antd/lib/typography/Title';
import React from 'react';


import { Info } from '../components/Info';
import { USER_VOCABS } from '../modules/userVocab/data';
import { HSK_VOCABS_BY_LEVEL } from '../modules/hsk';

export const WordsBankPage = () => {
  return (
    <>
      <Tabs size="small">
        <Tabs.TabPane tab="User" key="0">
          <VocabList vocabs={USER_VOCABS} />
        </Tabs.TabPane>
        {HSK_VOCABS_BY_LEVEL.map((vocabs, level) =>
          <Tabs.TabPane tab={`HSK ${level + 1}`} key={(level + 1).toString()}>
            <VocabList vocabs={vocabs} />
          </Tabs.TabPane>
        )}
      </Tabs>
    </>
  );
}

const VocabList = ({ vocabs }: { vocabs: Vocab[] }) => {

  return (
    <List
      className="m-md"
      dataSource={vocabs}
      pagination={{ pageSize: 25, position: 'both' }}
      renderItem={(vocab) => {
        return (
          <Card key={vocab.hanzi} className="my-md">
            <Title level={2}>
              {vocab.hanzi}
            </Title>
            <Info title="pīnyīn">
              {vocab.pinyin}
            </Info>
            <Info title="Definition">
              {vocab.translations.join('; ')}
            </Info>
          </Card>
        );
      }}
    />
  );
}
